.aboutUsPic {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

#pic {
  border-radius: 15px;
  border: 4px solid saddlebrown;
  position: absolute;
  top: 200px;
}

#namesake {
  border: 4px solid saddlebrown;
  border-radius: 15px;
  background-color: grey;
  color: white;
  position: absolute;
  text-align: center;
  width: 230px;
  font-size: 200%;
  top: -400px;
  left: 320px;
}

#bio {
  border: 4px solid saddlebrown;
  border-radius: 15px;
  background-color: grey;
  color: white;
  position: absolute;
  top: -400px;
  text-align: center;
  margin-top: -200px;
  width: 230px;
  left: 500px;
}
