.saveItem {
  border-radius: 15px;
  background-color: grey;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.saveItem:hover {
  background-color: green;
}

.savedItem {
  color: green;
}

.item {
  background: none;
  border: none;
  margin: 15px;
  border-radius: 15px;
  font-size: larger;
}

#remove {
  position: relative;
  margin-top: 2px;
  width: 260px;
  height: 280px;
  border: 3px solid rgb(194, 3, 3);
  border-radius: 15px;
  padding: 3px;
}

.iPic {
  position: relative;
  margin-top: 2px;
  width: 290px;
  height: 300px;
  border: 3px solid rgb(194, 3, 3);
  border-radius: 15px;
  padding: 3px;
}

.removeItem {
  border-radius: 15px;
  background-color: grey;
  color: white;
}

.removeItem:hover {
  border-radius: 15px;
  background-color: rgb(194, 3, 3);
  color: white;
}
