.checkout {
  border: 3px solid saddlebrown;
  border-radius: 15px;
  position: absolute;
  color: white;
  background-color: grey;
  left: 1000px;
  top: 200px;
  width: 300px;
  padding-left: 10px;
  margin-bottom: 10px;
}

.savedItems {
  margin-right: 300px;
  margin-top: 60px;
}

.exit {
  margin-left: 100px;
  margin-bottom: 10px;
  border-radius: 15px;
  font-size: medium;
  background-color: darkorange;
  color: navy;
}

.exit:hover {
  width: 150px;
  height: 30px;
  background-color: green;
  color: white;
  margin-left: 70px;
  margin-right: 60px;
}

.checkedItems {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  position: relative;
  top: -40px;
}

.form {
  border: 3px solid saddlebrown;
  border-radius: 15px;
  position: absolute;
  text-align: center;
  background-color: grey;
  left: 270px;
  top: 200px;
  width: 400px;
  height: 30px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.formElement {
  margin-top: 3px;
  color: white;
}

#input {
  color: black;
  border-radius: 15px;
}

#submit {
  border-radius: 15px;
  font-size: medium;
  background-color: darkorange;
  color: navy;
  margin-left: 10px;
}

#submit:hover {
  background-color: green;
  color: white;
}

.cInfo {
  color: white;
  background-color: grey;
  text-align: center;
  border: 3px solid saddlebrown;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  position: relative;
  margin-left: 270px;
  margin-top: 60px;
  width: 370px;
  border-radius: 15px;
}

#resetC {
  border-radius: 15px;
  font-size: medium;
  background-color: darkorange;
  color: navy;
  margin-bottom: 5px;
}

#resetC:hover {
  background-color: green;
  color: white;
}

#orderInfoTitle {
  font-size: larger;
  font-style: italic;
}

.total {
  margin-left: 0px;
  font-size: 200%;
}

.elementTotal {
  display: inline-block;
}

#eTotalT {
  text-align: left;
}

#eTotalM {
  position: absolute;
  left: 250px;
}

#eTotal {
  position: absolute;
  left: 200px;
}

.preT {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 130%;
}
